                  <div class="collapse_title">第一級處理</div>
                  <br />去除污水中呈懸浮狀況的
                  <b style="color: red">固體污染物質</b
                  >，物理處理法大部分只能完結一級處理的要求。
                  <br />通過一級處理的污水，生化需氧量 (BOD)
                  一般可去除30%左右，但仍達不到排放規範。
                </div>
              </div>
            </div>
          </div>

          <div class="col-xl-4">
