                  <br />去除污水中呈膠體和溶解狀況的
                  <span style="color: red; font-weight: 600">有機污染物質</span>
                  <br />- 1. 生化需氧量( BOD ) <br />2. 化學需氧量( COD
                  )，去除率可達90%以上，使有機污染物到達排放規範。
                </div>
              </div>
            </div>
          </div>

          <div class="col-xl-4">
