<script>
// import Chart from "chart.js";
import axios from "axios";
import $ from "jquery";
axios.defaults.headers.common["Content-Type"] =
  "application/x-www-form-urlencoded";
axios.defaults.headers.common["Access-Control-Allow-Origin"] = "*";
export default {
  mounted() {
    this.dirtywater();
    this.dialogAnimation();
    this.test();
  },
  data() {
    return {
      searchType: [
        { name: "公共污水下水道普及率", columnName: "public" },
        { name: "專用污水下水道普及率", columnName: "special" },
        { name: "建築物污水設施設置率", columnName: "building" },
        { name: "整體污水處理率合計", columnName: "total" },
        { name: "綜合圖表", columnName: "mix" },
      ],
      chartyearArray: ["108", "107", "106", "105", "104"],
      ChartOption: {
        title: {
          text: "", //主標題
          textStyle: {
            color: "#006030",
            fontSize: 16,
            align: "center",
          },
          subtext: "", //副標題
          subtextStyle: {
            color: "#642100",
            fontSize: 14,
            align: "center",
          },
          itemGap: 7,
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
        },
        xAxis: {
          type: "category",
          data: [],
        },
        yAxis: {
          type: "value",
          max: 0,
          min: 0,
        },
        series: [
          {
            name: "",
            data: [],
            type: "line",
          },
        ],
      },
      chartyear: [],
      mixchartyear: "",
      //tooltip顯示的內容設定
      tooltipName: [],
      //tooltip彈框設定
      tooltipOptions: {
        permanent: true,
        offset: [0, -10],
      },
      iconExplain: ["初級處理", "生物二級處理", "三級處理"],
      // ---------------------水庫資料---------------------
      text: [],
      // ---------------------地圖資料---------------------
      //地圖縮放比例
      zoom: 10,
      //地圖中心
      center: [22.732786, 120.342959],
      // 地標圖示
      iconimg: [
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-blue.png",
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-green.png",
