      MapData: [
        {
          name: "中區污水處理廠",
          local: [22.586334, 120.287168],
        },
        {
          name: "鳳山溪污水處理廠",
          local: [22.598869, 120.351317],
        },
        {
          name: "大樹污水處理廠",
          local: [22.686997, 120.434006],
        },
        {
          name: "楠梓污水處理廠",
          local: [22.728425, 120.269288],
        },
        {
          name: "旗美污水處理廠",
          local: [22.879226, 120.496331],
        },
      ],
      chart: "",
      ctx: "",
      animation: "",
      animation2: "",
    };
  },
  destroyed() {
