  },
  methods: {
    // 取得汙水處理廠資料
    async test() {
      // const cors = "https://cors-anywhere.herokuapp.com/";
      let data = {
        station_id: "2075",
        startdate: "2018/01/02",
        enddate: "2019/01/02",
      };
      // let config = {
      //   headers: { "Content-Type": "application/x-www-form-urlencoded" },
      // };
      let headers = {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": true,
      };
      axios.defaults.headers.post["Content-Type"] =
        "application/x-www-form-urlencoded";
      axios.defaults.headers.common["Access-Control-Allow-Origin"] = "*";
      await axios
        .post("/test", data, headers)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 取出汙水處理廠名稱，以顯現在地圖的tooltip上
    ready(item) {
      let name = item.name.split("");
      name = name[0] + name[1];
      this.tooltipName.push(name);
    },
    // 根據汙水處理廠處理等級，更改地圖上icon顏色
    iconColor(index) {
      if (this.text[index].處理等級 === "初級處理") {
        return this.iconimg[0];
      } else if (this.text[index].處理等級 === "生物二級處理") {
        return this.iconimg[1];
      } else {
        return this.iconimg[2];
      }
    },
    // 取得汙水處理廠資料
    async dirtywater() {
      await axios
        .get("server/dirtywater.json")
        .then((res) => {
          this.text = res.data;
          console.log(this.text);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 對話框動畫
    dialogAnimation() {
      $(".toast").toast("show");
      this.animation = setInterval(function () {
        setTimeout(function () {
          $(".toast").toast("hide");
          $("#toast1").toast("show");
        }, 1000);
        setTimeout(function () {
          $("#toast2").toast("show");
        }, 1400);
        setTimeout(function () {
          $("#toast3").toast("show");
        }, 1800);
        setTimeout(function () {
          $("#toast4").toast("show");
        }, 2200);
        $(".toast").toast("hide");
      }, 5000);
    },
    // 資料查詢
    // --------------          傳入參數說明:          --------------
    //      dbname    ||     name   ||        index      ||   type
    //   資料庫欄位名稱 ||  中文名稱  ||   點選第幾個項目 ||   月/年查詢
    //      public || 公共污水下水道普及率 ||    0    ||       month    ---範例
    searchChart(dbname, name, index, type) {
      console.log(this.chartyear[index]);
      console.log(dbname);
      console.log(name);
      this.ChartOption.title.text = name; // 設置圖表title
      this.ChartOption.series[0].name = name;
      this.ChartOption.xAxis.data = [];
      this.ChartOption.series = [
        {
          name: "",
          data: [],
          type: "line",
        },
      ];
      var mixDetail = [
        { name: "公共污水下水道普及率", data: [], type: "line" },
        { name: "專用污水下水道普及率", data: [], type: "line" },
        { name: "建築物污水設施設置率", data: [], type: "line" },
        { name: "整體污水處理率合計", data: [], type: "line" },
      ];
      switch (type) {
        case "month":
          axios
            .post("/api/dirtywater", {
              year: this.chartyear[index],
            })
            .then((res) => {
              console.log(res);
              this.ChartOption.yAxis.min = parseInt(res.data[0][dbname]);
              res.data.forEach((item) => {
                if (this.ChartOption.yAxis.max < item[dbname]) {
                  this.ChartOption.yAxis.max = parseInt(item[dbname]);
                }
                if (this.ChartOption.yAxis.min > item[dbname]) {
                  this.ChartOption.yAxis.min = parseInt(item[dbname]);
                  console.log(this.ChartOption.yAxis.min);
                }
                this.ChartOption.title.subtext =
                  this.chartyear[index] + "年資料"; // 設置圖表副title
                this.ChartOption.xAxis.data.push(item.date + "月");
                if (dbname !== "mix") {
                  this.ChartOption.series[0].data.push(item[dbname]);
                } else {
                  mixDetail[0].data.push(item["public"]);
                  mixDetail[1].data.push(item["special"]);
                  mixDetail[2].data.push(item["building"]);
                  mixDetail[3].data.push(item["total"]);
                }
              });
              if (dbname === "mix") {
                this.ChartOption.series = mixDetail;
              }
              this.ChartOption.yAxis.max = this.ChartOption.yAxis.max + 5;
              this.ChartOption.yAxis.min = this.ChartOption.yAxis.min - 3;
              if (this.ChartOption.yAxis.min < 0) {
                this.ChartOption.yAxis.min = 0;
              }
              console.log(this.ChartOption.yAxis.max);
              console.log(this.ChartOption.yAxis.min);
            })
            .catch((err) => {
              console.log(err);
            });
          break;
        case "year":
          axios
            .post("/api/dirtywater", { average: 1 })
            .then((res) => {
              console.log(res);
              res.data.forEach((item) => {
                this.ChartOption.title.subtext = "104 - 108年資料";
                this.ChartOption.xAxis.data.push(item.year + "年");
                if (dbname !== "mix") {
                  this.ChartOption.series[0].data.push(item[dbname]);
                } else {
                  mixDetail[0].data.push(item["public"]);
                  mixDetail[1].data.push(item["special"]);
                  mixDetail[2].data.push(item["building"]);
                  mixDetail[3].data.push(item["total"]);
                }
              });
              if (dbname === "mix") {
                this.ChartOption.series = mixDetail;
              }
            })
            .catch((err) => {
              console.log(err);
            });
          break;
      }
    },
